"use client";
import axios from "axios";
import { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { format } from "date-fns";
import Header from "@/app/components/header";
import AirPollution from "@/app/components/widget/airPollution";
import Sun from "@/app/components/widget/sun";
import Feel from "@/app/components/widget/feelLike";
import CurrentWeather from "@/app/components/widget/currentWeather";
import { City, HourlyForecastData, AirQualityData } from "@/lib/types";

export default function Dashboard() {
  const [city, setCity] = useState("Boac");
  const [weather, setWeather] = useState<HourlyForecastData | null>(null);
  const [airQuality, setAirQuality] = useState<AirQualityData | null>(null);
  const [currentCity, setCurrentCity] = useState<City | null>(null);
  const [loading, setLoading] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  const getWeather = async (cityName: string) => {
    setLoading(true);
    try {
      const response = await axios.get(
        `http://localhost:5000/get-weather?city=Manila`
      );

      console.log("Weather data:", response.data.current);
      setWeather(response.data.current);
      setAirQuality(response.data.air_pollution.list[0]);
      setCurrentCity({
        id: response.data.current.id,
        name: response.data.current.name,
        coord: response.data.current.coord,
        country: response.data.current.sys.country,
        timezone: response.data.current.timezone,
        sunrise: response.data.current.sys.sunrise,
        sunset: response.data.current.sys.sunset,
      });
      
    } catch (error) {
      console.error("Error fetching weather data:", error);
      setWeather(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getWeather(city);
  }, [city]);

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  // // Format the sunrise and sunset time for display
  // const formatTime = (timestamp: number) => {
  //   const date = new Date(timestamp * 1000);
  //   return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  // };

  return (
    <div
      className={`min-h-screen flex flex-col py-10 px-10 transition-all duration-500 ${
        darkMode ? "bg-gray-900 text-white" : "bg-white text-black"
      }`}
    >
      <header className="mb-8">
        <h1 className="text-4xl font-bold">
          <Header darkMode={darkMode} toggleDarkMode={toggleDarkMode} />
        </h1>
      </header>

      <div className="w-full flex flex-row gap-4">
        {airQuality && (
          <AirPollution airQuality={airQuality} className="w-96 h-40" />
        )}
        {/* {weather && <CurrentWeather data={weather} city={currentCity} />} */}
        {/* Sun Component */}
        {currentCity && (
          <Sun
            sun={currentCity} 
          />
        )}
        {/* Feel Component */}
        {weather && <Feel feel={weather} />}
      </div>
    </div>
  );
}
